@import url(https://fonts.googleapis.com/css2?family=Antic+Didone&family=Maven+Pro&family=Montez&display=swap);
*,*:before,*:after{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-size:1.6rem}a{text-decoration:none}.header{height:6rem;width:100%;padding:0 2rem;border-bottom-left-radius:1rem;border-bottom-right-radius:1rem;display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:1fr;align-items:center;background-color:#08c7bb;box-shadow:0 2px 10px #08c7bb}.header__logo{grid-area:1/2/1/3;justify-self:center;font-family:"Montez",cursive;font-size:clamp(2.8rem, 3.5vw, 3.6rem);color:#fff}.header__btn--back{grid-area:1/1/1/2;justify-self:start;border:none;background:none;cursor:pointer;outline:none}.header__back{color:#fff}.header__btn--cart{grid-area:1/3/1/4;justify-self:end;border:none;background:none;cursor:pointer;outline:none}.header__cart{color:#fff}.badge span{border:.1rem solid #fff;font-family:"Maven Pro",sans-serif;font-size:clamp(1.2rem, 1.4vw, 1.4rem);background-color:#08c7bb;color:#fff}@media(min-width: 768px){.header{height:7rem;padding:0 4rem}}.product{width:32rem;height:40rem;margin:0 auto;margin-bottom:4rem;padding:2rem;border:.1rem solid #08c7bb;border-radius:1rem;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.product__image{width:10rem;height:15rem}.product__title{margin:2rem 0;display:-webkit-box;overflow:hidden;-webkit-line-clamp:2;-webkit-box-orient:vertical;font-family:"Maven Pro",sans-serif;font-size:1.8rem;font-weight:900;text-align:center;color:#08c7bb}.product__desc{margin-bottom:2rem;display:-webkit-box;overflow:hidden;-webkit-line-clamp:4;-webkit-box-orient:vertical;font-family:"Maven Pro",sans-serif;font-size:1.4rem;text-overflow:ellipsis;color:#08c7bb}.product__cart-group{width:100%;display:flex;justify-content:space-between;align-items:center}.product__cart-group--price{font-family:"Maven Pro",sans-serif;font-size:2.4rem;color:#08c7bb}.product__cart-group--add-to-cart,.product__cart-group--add-more{border:.1rem solid #08c7bb;border-radius:.5rem;padding:1rem;background-color:#fff;font-family:"Maven Pro",sans-serif;font-size:1.4rem;color:#08c7bb;cursor:pointer}.product__cart-group--add-to-cart:hover,.product__cart-group--add-more:hover{border-color:#fff;background-color:#08c7bb;color:#fff}.product__cart-group--add-more{background-color:#08c7bb;color:#fff}.snackbar div{margin:0 auto;font-family:"Maven Pro",sans-serif;font-size:clamp(1.4rem, 1.5vw, 1.8rem);font-weight:800;background-color:#08c7bb;color:#fff}@media(min-width: 768px){.product{justify-self:center;margin:0;margin-bottom:7rem}}.total-card{width:32rem;height:17.2rem;border-radius:1rem;padding:2rem;margin:5rem 0 4rem 0;font-family:"Maven Pro",sans-serif;font-size:2.4rem;font-weight:400;background-color:#fff;color:#08c7bb;letter-spacing:.1rem}.total-card__items,.total-card__actions,.total-card__amount{margin-bottom:2rem;display:flex;justify-content:space-between;align-items:center}.total-card__actions--checkout{border:.1rem solid #08c7bb;border-radius:.5rem;padding:1rem;font-size:1.4rem;background-color:#fff;color:#08c7bb;cursor:not-allowed}.total-card__actions--clear{border:none;font-size:1.4rem;background:none;color:#08c7bb;cursor:pointer}.total-card__actions--clear:hover{text-decoration:underline}.total-card__actions--clear:visited{color:#08c7bb}.cart-product{width:32rem;height:17.2rem;border:.1rem solid #fff;border-radius:1rem;padding:2rem;margin-bottom:4rem;display:flex;flex-direction:column;justify-content:space-between;align-content:center}.cart-product__name{font-family:"Maven Pro",sans-serif;font-size:1.8rem;text-align:center;color:#fff}.cart-product__actions{display:flex;justify-content:space-between;align-items:center}.cart-product__actions--amount{font-family:"Maven Pro",sans-serif;font-size:2.4rem;color:#fff}.cart-product__actions--shop{display:flex;justify-content:space-between;align-items:center}.product__inc,.product__dec{border:none;border-radius:50%;padding:.5rem .8rem;font-family:"Maven Pro",sans-serif;font-size:2.4rem;text-align:center;background-color:#fff;color:#08c7bb;cursor:pointer}.product__quantity{padding:0 .8rem;font-family:"Maven Pro",sans-serif;font-size:2.4rem;color:#fff}.products--loading{width:100%;min-height:calc(100vh - 6rem);display:grid;justify-content:center;align-items:center}@media(min-width: 768px){.products--loading{min-height:calc(100vh - 7rem)}}.products{width:100%;min-height:calc(100vh - 6rem);padding:4rem 2rem 0 2rem}.products__title{max-width:20ch;margin:0 auto;margin-bottom:4rem;font-family:"Antic Didone",serif;font-size:clamp(3rem, 4vw, 4.8rem);color:#08c7bb;text-align:center}.products__listing{max-width:100%;width:100%}@media(min-width: 768px){.products{min-height:calc(100vh - 7rem);padding:7rem 4rem 0 4rem}.products__title{margin-bottom:7rem}.products__listing{display:grid;grid-template-columns:1fr 1fr}}@media(min-width: 1280px){.products__listing{grid-template-columns:1fr 1fr 1fr}}.cart{position:fixed;top:5rem;left:0;padding-top:1rem;height:calc(100vh - 5rem);width:100%;display:flex;flex-direction:column;align-items:center;background-color:#08c7bb;overflow-y:scroll;transition:left 300ms ease-in-out}.cart__products{display:grid;grid-template-columns:1fr}.cart--invis{left:100vw}@media(min-width: 768px){.cart{top:6rem}.cart__products{grid-template-columns:repeat(2, 1fr);grid-column-gap:5rem}}@media(min-width: 1280px){.cart__products{grid-template-columns:repeat(3, 1fr);grid-column-gap:9rem}}.empty-cart{position:fixed;top:5rem;left:0;padding-top:1rem;height:calc(100vh - 5rem);width:100%;display:flex;flex-direction:column;justify-content:space-between;align-items:center;background-color:#08c7bb;transition:left 300ms ease-in-out}.empty-cart__title{margin:5rem auto 2rem auto;padding:0 2rem;font-family:"Antic Didone",serif;font-size:clamp(3rem, 4vw, 4.8rem);color:#fff;letter-spacing:.8rem;text-align:center}.empty-cart__subtitle{margin:0 auto;font-family:"Maven Pro",sans-serif;font-size:clamp(1.8rem, 2vw, 2.4rem);color:#fff;letter-spacing:.2rem;text-align:center}.empty-cart__img{width:clamp(36rem, 45vw, 50rem);height:auto}@media all and (min-width: 768px){.empty-cart__title{margin:8rem auto 4rem auto}}
